<template>
	<div>
		<svg id="pl1svg" :width="width" :height="height"></svg>
	</div>
</template>

<script>
	import * as d3 from 'd3';
	export default {
		data() {
			return {
				width: 400,
				height: 270,
				chartData: [
					{"name":"4/11", "value":600},
					{"name":"4/12", "value":400},
					{"name":"4/15", "value":800},
					{"name":"4/16", "value":730},
					{"name":"4/17", "value":500},
					{"name":"4/17", "value":200},
					{"name":"4/19", "value":400}
				]
			}
		},
		mounted() {
			this.initChart();
		},
		methods: {
			initChart() {
				var that = this;
				let chartData = that.chartData;
				var pl1svg = d3.select("#pl1svg");
				let min = d3.min(chartData, d => d.value) - 200;
				let padding = {
					width: that.width,
					height: that.height,
					left: 30,
					right: 30,
					top: 20,
					bottom: 20
				}
				let max = d3.max(chartData, d => d.value) + 200
				let xScale = d3.scaleBand()
					.domain(d3.range(chartData.length))
					.range([0, padding.width - padding.left - padding.right])
				let yScale = d3.scaleLinear()
					.domain([0, max])
					.range([0, padding.height - padding.top - padding.bottom])
				let yScaleAxis = d3.scaleLinear()
					.domain([0, max]) //定义从0开始
					.range([padding.height - padding.top - padding.bottom, 0]) // 坐标轴值域取反，因为坐标原点在左上角
				// 设置x轴 y轴
				let xAxis = d3.axisBottom().scale(xScale).tickSize(0)
				let yAxis = d3.axisLeft(yScaleAxis);
				//添加纵轴网格线
				yAxis.tickSize(-(padding.width - padding.left - padding.right + 10)).ticks(5);
				// 添加x轴
				var xBar = pl1svg.append('g')
					.attr('class', 'axis')
					.attr('transform', 'translate(' + padding.left + ',' + (padding.height - padding.bottom) + ')')
					.call(xAxis)
				xBar.selectAll("text")
					.attr("fill", "#000")
					.attr("font-size", 13)
					.attr("font-weight", 300)
					.text(function(d) {
						return chartData[d].name;
					});
				var yInnerBar = pl1svg.append("g")
					.attr('class', 'yaxis')
					.attr("transform", "translate(" + (padding.left) + "," + (padding.top) + ")")
					.call(yAxis);
				// 定义折线生成器
				var line = d3.line()
					.x(function(d, i) {
						return xScale(i)
					})
					.y(function(d) {
						return yScaleAxis(d.value);
					});
				var pathg = pl1svg.append("g");
				pathg.attr("transform","translate("+(padding.left+xScale.bandwidth()/2)+","+padding.top+")")
				pathg.append("path")
					.attr("d",line(chartData))
					.attr("fill", "none")
					.attr("stroke", "#000")
					.attr("stroke-width", 2);

			}
		}
	}
</script>

